<template>
    <div>
        <div class="btn-group" role="group" aria-label="Button group with nested dropdown">
        <button type="button" class="btn" @click="triggerPage(index)" v-for="(value, index) in page_index_list" :key="index" :class="{'btn-secondary': index!=current_page_index, 'btn-primary': index==current_page_index}">{{value}}</button>

        <div class="btn-group" role="group">
            <button id="btnGroupDrop1" type="button" class="btn btn-secondary dropdown-toggle" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
            数据集
            </button>
            <div class="dropdown-menu" aria-labelledby="btnGroupDrop1">
            <a class="dropdown-item" href="#" @click="triggerDataset(dataset_name)" v-for="(dataset_name, dataset_index) in dataset" :key="dataset_index">{{dataset_name}} <span class="badge badge-light">{{sample_num_in_dataset[dataset_name]}}</span></a>
            </div>
        </div>
        </div>

        <div>
            <div class="row" style="margin-top:50px">
                <div class="col-lg-6">
                    <div class="input-group">
                        <div class="input-group-btn">
                            <button type="button" class="btn btn-default dropdown-toggle" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">{{spider_source}} <span class="caret"></span></button>
                            <ul class="dropdown-menu">
                                <li v-for="(s, s_i) in spider_source_list" :key="s_i">
                                    <a href="#" @click="spiderChange(s)">{{s}}</a>
                                </li>
                            </ul>
                        </div><!-- /btn-group -->

                        <input type="text" class="form-control" aria-label="..." v-model="spider_keyword">
                        <span class="input-group-btn">
                            <button class="btn btn-default" type="button" @click="searchChange()">Go!</button>
                        </span>
                    </div><!-- /input-group -->
                </div><!-- /.col-lg-6 -->
            </div><!-- /.row -->

            <div style="display:flex; margin-top:20px">
                <span class="badge badge-info">等待处理: {{waiting_process_num}}</span>
                <span class="badge badge-info" style="margin-left:20px">完成处理: {{finished_process_num}}</span>
            </div>
        </div>
        <div class="data-and-tag-layout">
            <div v-for="(group, group_index) in page_content" :key="group_index">
                <div class="data-and-tag-card">
                    <div style="align-self:flex-end; margin-right:20px">
                        <span class="badge badge-info">{{current_dataset}}</span>
                    </div>
                    <div class="data-and-tag-group">
                        <div class="group-data-cell">
                            <!-- 数据显示 -->
                            <div class="data-cell" v-for="(cell, index) in group['data']" :key="index">
                                <div class="data-item">
                                    <img v-if="cell.type == 'IMAGE' " :src="cell.data" style="max-height:256px"/>
                                    <a v-else-if="cell.type == 'FILE'" :href="cell.data">{{cell.title}}</a>
                                    <a v-else-if="cell.type == 'JSON'" :href="cell.data">{{cell.title}}</a>
                                    <p v-else>{{cell.data}}</p>
                                </div>
                            </div>
                        </div>

                        <div class="group-tag-cell">
                            <!-- 打标签 -->
                            <div v-for="(tag_name, tag_index) in tags" :key="tag_index" style="margin-right: 10px">
                                <label>{{tag_name}}</label>
                                <input type="checkbox" :checked="group.tag.indexOf(tag_name) != -1" @click="tagChange(group_index, tag_name)">
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    name: 'ExperimentResults', 
    data(){
        return {
            dataset: [],
            sample_num_in_dataset: {},
            page_index_list: [],
            page_content: [],
            current_page_index: 0,
            current_dataset: '',
            tags:[],
            is_spider: false,
            spider_source: 'BAIDU',
            spider_source_list: ['BAIDU','BING'],
            spider_keyword: '',
            waiting_process_num: 0,
            finished_process_num: 0
        }
    },
    mounted: function(){
        var _this = this;
         this.$axios.get(this.BASE_URL+'/batch-api/entry/', {
             params:{

             }
         }).then(function(res){
                // response_content = {
                //     'num_in_page': num_in_page,
                //     'page_num': page_num,
                //     'page_index': page_index,
                //     'dataset_name': dataset_names,
                //     'result_num': result_num,
                //     'result_list': dataset_and_results[start_index: end_index+1]
                // }
                var response_content = res.data.content;
                _this.dataset = response_content['dataset_name'];
                _this.page_index_list = []
                for(var i=0; i<response_content['page_num']; ++i){
                    _this.page_index_list.push(i);
                }
                _this.page_content = response_content['result_list'];
                _this.current_page_index = 0;
                _this.sample_num_in_dataset = response_content['sample_num_in_dataset']
                _this.current_dataset = response_content['current_dataset_name'];
                _this.tags = response_content['tags'];
                // 正在等待处理样本数
                _this.waiting_process_num = response_content['waiting']
                // 已经完成处理的样本数
                _this.finished_process_num = response_content['finished']
                // 当前spider源
                _this.spider_source = response_content['spider']
                console.log( _this.current_dataset)
         })
    },
    methods:{
        triggerPage: function(p){
            var _this = this;
            this.$axios.get(this.BASE_URL+'/batch-api/page/', {
                params:{
                    dataset_name: _this.current_dataset,
                    page_index: p
                }
            }).then(function(res){
                    // response_content = {
                    //     'num_in_page': num_in_page,
                    //     'page_num': page_num,
                    //     'page_index': page_index,
                    //     'dataset_name': dataset_names,
                    //     'result_num': result_num,
                    //     'result_list': dataset_and_results[start_index: end_index+1]
                    // }
                    _this.page_content = res.data.content['result_list'];
                    _this.current_page_index = p;
                    // 正在等待处理样本数
                    _this.waiting_process_num = response_content['waiting']
                    // 已经完成处理的样本数
                    _this.finished_process_num = response_content['finished']
                    console.log('Switch to page '+p);
            })
        },
        triggerDataset: function(d){
            var _this = this;
            this.$axios.get(this.BASE_URL+'/batch-api/entry/', {
                params:{
                    dataset_name: d
                }
            }).then(function(res){
                    // response_content = {
                    //     'num_in_page': num_in_page,
                    //     'page_num': page_num,
                    //     'page_index': page_index,
                    //     'dataset_name': dataset_names,
                    //     'result_num': result_num,
                    //     'result_list': dataset_and_results[start_index: end_index+1]
                    // }

                    _this.dataset = res.data.content['dataset_name'];
                    _this.page_index_list = []
                    for(var i=0; i<res.data.content['page_num']; ++i){
                        _this.page_index_list.push(i);
                    }
                    _this.page_content = res.data.content['result_list'];
                    _this.current_page_index = 0;
                    _this.sample_num_in_dataset = res.data.content['sample_num_in_dataset']
                    _this.current_dataset = res.data.content['current_dataset_name'];

                    console.log('Switch to dataset '+d);
            })
        },
        tagChange: function(index, tag_name){
            var tag_index = this.page_content[index]['tag'].indexOf(tag_name);
            if(tag_index == -1){
                // 添加标记
                this.page_content[index]['tag'].push(tag_name);
            }
            else{
                // 删除标记
                this.page_content[index]['tag'].splice(tag_index, 1);
            }
            
            // 更新后台数据标签
            var params = new URLSearchParams();            
            params.append('page_index', this.current_page_index);
            params.append('data_index', index);
            params.append('data_tag', JSON.stringify(this.page_content[index]['tag']));
            this.$axios.post(this.BASE_URL + '/batch-api/tag/', params).then(function(res){
                // do nothing
            });

            // 更新页面
            this.$forceUpdate();
        },
        spiderChange: function(s){
            this.spider_source = s;
        },
        searchChange: function(){
            // 网址
            var url = this.spider_source;
            var keyword = this.spider_keyword;

            var _this = this;
            var params = new URLSearchParams();            
            params.append("url", url);
            params.append("keyword", keyword);
            this.$axios.post(this.BASE_URL + '/batch-api/search/', params).then(function(res){
                // do nothing
            });
        }
    }
}
</script>

<style scoped>
.data-gallery-grid{
    display: flex;
    flex-direction: column;
}
.operator-layout{
}
.data-and-tag-layout{
    display: flex;
    flex-direction: column;
}
.group-data-cell{
    display: flex;
    flex-direction: row;
    margin-right: 20px;
}
.data-cell{
    display: flex;
    flex-direction: column;
    margin-right: 20px;
}
.data-item{

}
.group-tag-cell{
    display: flex;
    flex-direction: column;
    justify-content: start;
}
.data-and-tag-group{
    display: flex;
    justify-content: space-around;
}
.data-and-tag-card{
    border: 1px solid #9999;
    margin-top: 10px;
    display: flex;
    justify-content: center;
    padding-top: 10px;
    flex-direction: column;
}

/* .data-and-tag-card:nth-child(2){
    align-self: center;
} */
.tag-attr{
    margin-top: 5px;
    display: flex;
    justify-content: space-between;
}
</style>


